import React, { useEffect } from 'react'
import './Header.css'
import { NavbarBrand } from 'react-bootstrap'
import Logo from '../Assets/logo-sae-removebg-preview.png'
import BitLogo from '../Assets/BitLogo.png'
import { NavLink } from 'react-router-dom'
import $ from 'jquery'

function Header() {
    function animation() {
        var tabsNewAnim = $('#navbarSupportedContent')
        var activeItemNewAnim = tabsNewAnim.find('.active')
        var activeWidthNewAnimHeight = activeItemNewAnim.innerHeight()
        var activeWidthNewAnimWidth = activeItemNewAnim.innerWidth()
        var itemPosNewAnimTop = activeItemNewAnim.position()
        var itemPosNewAnimLeft = activeItemNewAnim.position()
        $('.hori-selector').css({
            top: itemPosNewAnimTop.top + 'px',
            left: itemPosNewAnimLeft.left + 'px',
            height: activeWidthNewAnimHeight + 'px',
            width: activeWidthNewAnimWidth + 'px',
        })
        $('#navbarSupportedContent').on('click', 'li', function (e) {
            $('#navbarSupportedContent ul li').removeClass('active')
            $(this).addClass('active')
            var activeWidthNewAnimHeight = $(this).innerHeight()
            var activeWidthNewAnimWidth = $(this).innerWidth()
            var itemPosNewAnimTop = $(this).position()
            var itemPosNewAnimLeft = $(this).position()
            $('.hori-selector').css({
                top: itemPosNewAnimTop.top + 'px',
                left: itemPosNewAnimLeft.left + 'px',
                height: activeWidthNewAnimHeight + 'px',
                width: activeWidthNewAnimWidth + 'px',
            })
        })
    }

    useEffect(() => {
        animation()
        $(window).on('resize', function () {
            setTimeout(function () {
                animation()
            }, 500)
        })
    }, [])

    return (
        <nav className="navbar navbar-expand-lg navbar-mainbg sticky-top">
            <NavbarBrand>
                <img
                    src={Logo}
                    width="40"
                    height="40"
                    className="d-inline-block align-top logo-x"
                    alt="React Bootstrap logo"
                />
                <img
                    src={BitLogo}
                    width="40"
                    height="40"
                    className="d-inline-block align-top logo-x"
                    alt="React Bootstrap logo"
                />
            </NavbarBrand>

            <button
                className="navbar-toggler"
                onClick={function () {
                    setTimeout(function () {
                        animation()
                    })
                }}
                type="button"
                data-toggle="collapse"
                data-target="#navbarSupportedContent"
                aria-controls="navbarSupportedContent"
                aria-expanded="false"
                aria-label="Toggle navigation"
            >
                <i className="fas fa-bars text-white"></i>
            </button>

            <div
                className="collapse navbar-collapse"
                id="navbarSupportedContent"
            >
                <ul className="navbar-nav ml-auto">
                    <div className="hori-selector">
                        <div className="left"></div>
                        <div className="right"></div>
                    </div>

                    <li className="nav-item active">
                        <NavLink className="nav-link" to="/" exact>
                            <i className="fas fa-tachometer-alt"></i>Home
                        </NavLink>
                    </li>

                    <li className="nav-item">
                        <NavLink className="nav-link" to="/about" exact>
                            <i className="far fa-address-book"></i>About
                        </NavLink>
                    </li>

                    <li className="nav-item">
                        <NavLink className="nav-link" to="/events" exact>
                            <i className="far fa-clone"></i>Events
                        </NavLink>
                    </li>
                    <li className="nav-item">
                        <NavLink className="nav-link" to="/sponsors" exact>
                            <i className="far fa-chart-bar"></i>Sponsors
                        </NavLink>
                    </li>
                    <li className="nav-item">
                        <NavLink className="nav-link" to="/contact" exact>
                            <i className="far fa-copy"></i>Contact Us
                        </NavLink>
                    </li>
                </ul>
            </div>
        </nav>
    )
}

export default Header
